@import url(https://fonts.googleapis.com/css?  family=Lora:400,400i,700,700i);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

*{box-sizing:border-box}html{height:100%}body{background-color:white;color:black;padding:0;height:100vh}.theme-options{margin:0px;text-align:right;padding:13px 10px;background-color:white;display:-webkit-flex;display:flex;width:100%}.theme-options div{cursor:pointer;width:20px;height:20px;border-radius:4px;margin:2px 10px;border:1px solid white;display:inline-block;opacity:0.5}.theme-options div.active{opacity:1}.theme-options #theme-white{background-color:#dbcbcb}.theme-options #theme-blue{background-color:#201650}.theme-options #theme-orange{background-color:#fcdfa9}.theme-options #theme-green{background-color:#0a520a}.theme-options #theme-purple{background-color:purple}.theme-options #theme-black{background-color:black}.App.theme-blue{background-color:#201650}.App.theme-blue .content-box{background-color:#201650}.App.theme-blue .content-box svg text{stroke:#b3e2f5}.App.theme-orange{background-color:#fcdfa9}.App.theme-orange .content-box{background-color:#fcdfa9}.App.theme-orange .content-box svg text{stroke:#ac162f}.App.theme-green{background-color:#0a520a}.App.theme-green .content-box{background-color:#0a520a}.App.theme-green .content-box svg text{stroke:#9af1bb}.App.theme-purple{background-color:purple}.App.theme-purple .content-box{background-color:purple}.App.theme-purple .content-box svg text{stroke:#e2b3f5}.App.theme-white{background-color:white}.App.theme-white .content-box{background-color:white}.App.theme-white .content-box svg text{stroke:black}.App.theme-black{background-color:black}.App.theme-black .content-box{background-color:black}.App.theme-black .content-box svg text{stroke:#ffa5d8}

.heading{
    font-size: 100px;
    font-weight: bold;
    padding: 0;
    margin: 18px;
    /* background-color: blue; */
}
.content-box{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    text-align: center;
    -webkit-justify-content: center;
            justify-content: center;
    /* background-color: black; */
    height: 90vh;
}

.radius{
    border-radius: 25px 25px 25px 25px;
    /* border-style: none; */
    outline: 0;
}
.width{
  width: 50%;
}
svg text {
    font-family: Lora;
    letter-spacing: 11px;
    stroke: #ffa5d8;
    font-size: 150px;
    font-weight: 700;
    stroke-width: 3;
    -webkit-animation: textAnimate 5s infinite alternate;
            animation: textAnimate 5s infinite alternate;
    
  }

  
  @-webkit-keyframes textAnimate {
    0% {
      stroke-dasharray: 0 50%;
      stroke-dashoffset:  20%;
      fill:hsl(27, 60%, 74%)
  
    }
    
    100% {
      stroke-dasharray: 50% 0;
      stroke-dashoffstet: -20%;
      fill: hsla(189, 68%, 75%,0%)
    }
    
  }

  
  @keyframes textAnimate {
    0% {
      stroke-dasharray: 0 50%;
      stroke-dashoffset:  20%;
      fill:hsl(27, 60%, 74%)
  
    }
    
    100% {
      stroke-dasharray: 50% 0;
      stroke-dashoffstet: -20%;
      fill: hsla(189, 68%, 75%,0%)
    }
    
  }
@media only screen and (max-width: 1358px){
  
  svg text{
    font-size: 110px;
  }    
}  
@media only screen and (max-width: 900px){
  
  svg text{
    font-size: 85px;
  }    
  .width {
    width: 90%;
  }
} 
@media only screen and (max-width: 440px){
  
  svg text{
    font-size: 75px;
  }    
  .width {
    width: 90%;
  }
}



